@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap");
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.app[data-theme=light] {
  --text: hsl(200, 15%, 8%);
  --input: hsl(0, 0%, 52%);
  --backgroundColor: hsl(0, 0%, 98%);
  --element: hsl(0, 0%, 100%);
}

.app[data-theme=dark] {
  --text: hsl(0, 0%, 100%);
  --input: hsl(0, 0%, 52%);
  --backgroundColor: hsl(207, 26%, 17%);
  --element: hsl(209, 23%, 22%);
}

@media (min-width: 0) {
  body {
    width: 100%;
    height: 100vh;
    font-family: "Nunito Sans", sans-serif;
  }
  #root {
    width: 100%;
    height: 100%;
  }
  .mainNav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 23vh;
    padding: 2rem;
    background-color: var(--element);
    color: var(--text);
  }
  .mainNav h1 {
    font-size: 21px;
    font-weight: 800;
  }
  .container {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: 3rem;
    background-color: var(--backgroundColor);
  }
  .container .countriesWrapper {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    justify-items: center;
    align-items: center;
    gap: 2rem;
  }
  .container .countriesWrapper.auto {
    grid-template-columns: auto;
  }
  .container .countriesWrapper .card {
    width: 100%;
    height: 100%;
    box-shadow: 0px 3px 32px 12px rgba(0, 0, 0, 0.35);
    background-color: var(--element);
    color: var(--text);
    cursor: pointer;
  }
  .container .countriesWrapper .card.flex {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    cursor: default;
  }
  .container .countriesWrapper .card h3 {
    font-weight: 800;
  }
  .container .countriesWrapper .card p {
    font-weight: 400;
  }
  .container .countriesWrapper .card .cardContent {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 2rem;
  }
  .container .countriesWrapper .card .cardContent h3 {
    font-size: 30px;
  }
  .container .countriesWrapper .card .cardContent p {
    font-weight: 600;
  }
  .container .countriesWrapper .card .cardContent p span {
    font-weight: 400;
    margin-left: 0rem;
    color: var(--input);
  }
  .container .countriesWrapper .card .cardContemtWrapperr {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 2rem;
  }
  .container .countriesWrapper .topImg {
    display: flex;
    align-items: center;
  }
  .container .countriesWrapper .topImg img {
    width: 100%;
    border-bottom: 4px solid rgba(53, 52, 52, 0.1);
  }
  .container .countriesWrapper .active {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 22vh;
    width: 100%;
  }
  .container .countriesWrapper .active img {
    width: 60%;
    box-shadow: 0px 2px 20px 15px rgba(0, 0, 0, 0.35);
  }
  .topSearchForm {
    width: 100%;
    margin-top: -4rem;
  }
  .mainForm {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    padding: 2rem;
    gap: 2rem;
  }
  .mainForm #goBack {
    width: 45%;
    height: 4vh;
    background-color: var(--element);
    outline: none;
    border: none;
    color: var(--text);
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 0px 3px 10px 5px rgba(0, 0, 0, 0.35);
  }
  .mainForm label {
    width: 100%;
  }
  .mainForm label input[type=text] {
    width: 100%;
    height: 5vh;
    background-color: var(--element);
    border: none;
    outline: none;
    padding-left: 3rem;
    color: var(--text);
    border-radius: 5px;
  }
  .mainForm select {
    cursor: pointer;
    margin-left: 0;
    padding-left: 1rem;
    background-color: var(--element);
    color: var(--text);
    width: 70%;
    height: 6vh;
    border: none;
    outline: none;
  }
  .raz {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }
  .themeLabel {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  .themeLabel span {
    margin-right: 1rem;
  }
  .themeLabel input[type=checkbox] {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
  }
  footer {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 6vh;
    background-color: var(--backgroundColor);
    color: var(--text);
  }
  footer a {
    color: rgb(75, 13, 13);
    text-decoration: none;
    font-weight: 700;
  }
}
